.header{background:#4b8232;padding:20px 0}.header__title{color:#fff}.header__content{display:grid;gap:20px;align-items:center;grid-template-columns:1fr}@media(min-width: 786px){.header__content{grid-template-columns:auto auto auto 1fr}}.header__link{text-decoration:none}.header__search{padding:10px 40px 10px 10px;border:none;width:100%}.header__form{position:relative}.header__form::before{content:"ðŸ”Ž";position:absolute;top:25%;transform:translateY(-50%);right:10px;width:20px;height:20px;transform:rotate(280deg)}.header__back{display:flex;justify-self:flex-start;gap:5px;align-items:center;padding:5px 8px;border-radius:5px;background:#fff;border:solid 2px #fff;font-size:16px;cursor:pointer;transition:background .3s,color .3s}.header__back:hover{background:rgba(0,0,0,0);color:#fff}.header__nav{justify-self:flex-start}@media(min-width: 786px){.header__nav{justify-self:flex-end;grid-column:4/5}}.nav__list{display:flex;align-items:center;gap:10px;list-style:none}.nav__link{text-decoration:none;color:#fff;text-transform:uppercase;font-weight:600;letter-spacing:2px}.nav__link:hover{text-decoration:underline}